* {
  color: white;
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
}
html {
  font-size: 16px;
}

.react-spinner {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #0b001a;

  span {
    box-shadow: 0px 0px 1vmax 0vmax rgba(152, 113, 156, 0.4),
      0px 0px 1vmax 0vmax rgba(131, 0, 255, 0.4);

    background: radial-gradient(circle at center, #df14fa 30%, #743fca 70%);
    background: -webkit-radial-gradient(
      circle at center,
      #b400cc 50%,
      #8300ff 50%
    );
  }
}

.App {
  scroll-behavior: smooth;
  background-color: #36244e;
}
